<template>
  <div
    class="relative flex flex-wrap gap-5 justify-center px-5 py-5 m-5 bg-fixed"
    style="
      background-image: url('https://source.unsplash.com/1600x900/?landscape');
    "
  >
    <Switch />
    <Spinner v-show="isLoading" />
    <Bar
      @click.stop.prevent="toggleModal(user)"
      v-show="!isCardMode"
      :user="user"
      v-for="user in onePageData.onePageList"
      :key="user.login.uuid"
    />
    <Card
      @click.stop.prevent="toggleModal(user)"
      v-show="isCardMode"
      :user="user"
      v-for="user in onePageData.onePageList"
      :key="user.login.uuid"
    />
    <Modal
      v-if="isModalOpen"
      class="md:max-w-md md:mx-auto m-2 p-2 fixed inset-x-0 z-50 top-1/2 transform -translate-y-1/2"
    />
    <Backdrop @click="toggleModal({})" />
  </div>
  <div class="flex gap-1 p-1 justify-center px-3 py-3 m-3">
    <Pagination
      class="flex opacity-80 gap-3"
      v-model="page"
      :range-size="1"
      :pages="totalPages"
      active-color="#4caf50"
      @updateModelValue="updatePage"
    />
  </div>
</template>

<script>
import { ref, reactive, onMounted, computed, defineComponent } from "vue";
import { useStore } from "vuex";
import Bar from "./../components/Bar.vue";
import Card from "./../components/Card.vue";
import Switch from "./../components/Switch.vue";
import Pagination from "./../components/Pagination.vue";
import Modal from "./../components/Modal.vue";
import Backdrop from "./../components/Backdrop.vue";
import Spinner from "./../components/Spinner.vue";
import userAPI from "./../apis/users";
import { Toast } from "./../util";
export default defineComponent({
  name: "UserList",
  components: {
    Bar,
    Card,
    Switch,
    Pagination,
    Modal,
    Backdrop,
    Spinner,
  },
  setup() {
    const store = useStore();
    const isCardMode = computed(() => store.state.cardModeModules.isCardMode);
    const isModalOpen = computed(
      () => store.state.modalOpenModules.isModalOpen
    );
    const isLoading = computed(() => store.state.loadingModules.isLoading);
    const userData = reactive({ userList: [] });
    // 先拉取 localStorage資料 減少等待
    const page = ref(1);
    const onePageData = reactive({ onePageList: [] });
    const userAmountPerPage = 30;
    const totalPages = ref(0);
    userData.userList = JSON.parse(localStorage.getItem("userList"));
    // fetch 更新資料庫 3010筆為前提
    async function fetchData() {
      try {
        store.dispatch("toggleLoading");
        const response = await userAPI.getUsers(3010);
        const data = response.data.results;
        if (response.status !== 200) {
          throw new Error();
        }
        // 存到localStorage
        localStorage.setItem("userList", JSON.stringify(data));
        userData.userList = [...data];
        totalPages.value = Math.ceil(userData.userList.length / userAmountPerPage)
        // 完成更新data 畫面的陣列也更新
        updatePage(page.value)
        Toast.fire({
          icon: "success",
          title: "歡迎使用，可透過右上角圖案切換呈現模式",
        });
      } catch (error) {
        Toast.fire({
          icon: "warning",
          title: "無法獲取資料，請稍後再試",
        });
        store.dispatch("toggleLoading");
      }
    }
    
    function toggleModal(data) {
      store.dispatch("toggleModal", data);
    }

    function getUsersByPage(amountPerPage, page, arr) {
      const startIndex = (page - 1) * amountPerPage;
      return arr.slice(startIndex, startIndex + amountPerPage);
    }

    function updatePage(params) {
      page.value = params;
      onePageData.onePageList = getUsersByPage(
        userAmountPerPage,
        params,
        userData.userList
      );
      store.dispatch("toggleLoading");
    }

    onMounted(() => {
      fetchData();
      onePageData.onePageList = getUsersByPage(
        userAmountPerPage,
        page,
        userData.userList
      );
    });

    return {
      Bar,
      Card,
      Switch,
      Pagination,
      Modal,
      Backdrop,
      Spinner,
      isLoading,
      onePageData,
      userAmountPerPage,
      isCardMode,
      isModalOpen,
      page,
      totalPages,
      toggleModal,
      updatePage,
    };
  },
});
</script>
