<template>
  <ul class="flex flex-wrap items-center list-none">
    <button
      @click="goToPrev"
      class="flex items-center relative w-6 h-6"
      :class="{'cursor-default': !isPrevControlsActive}"
      :disabled="isLoading || !isPrevControlsActive"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
        version="1.1"
        viewBox="0 0 24 24"
      >
        <path
          d="M15.41,16.58L10.83,12L15.41,7.41L14,6L8,12L14,18L15.41,16.58Z"
        />
      </svg>
    </button>
    <Button
      v-for="page in pagination"
      :key="`pagination-page-${page}`"
      :page="page"
      :current="modelValue"
      :active-color="activeColor"
      @update="updatePageHandler"
    />
    <button
      @click="goToNext"
      class="flex items-center relative w-6 h-6"
      :class="{'cursor-default': !isNextControlsActive}"
      :disabled="isLoading || !isNextControlsActive"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
        version="1.1"
        viewBox="0 0 24 24"
      >
        <path d="M8.59,16.58L13.17,12L8.59,7.41L10,6L16,12L10,18L8.59,16.58Z" />
      </svg>
    </button>
  </ul>
</template>

<script>
import { defineComponent, computed } from "vue";
import Button from "./../components/Button.vue";
import { useStore } from "vuex";
export default defineComponent({
  name: "VPagination",
  components: {
    Button,
  },
  props: {
    pages: {
      type: Number,
      default: 0,
    },
    rangeSize: {
      type: Number,
      default: 1,
    },
    modelValue: {
      type: Number,
      default: 0,
    },
    activeColor: {
      type: String,
      default: "#DCEDFF",
    },
  },
  emits: ["updateModelValue"],
  setup(props, { emit }) {
    const store = useStore();
    const isLoading = computed(() => store.state.loadingModules.isLoading);
    const pagination = computed(() => {
      const res = [];
      const minPaginationElems = 5 + props.rangeSize * 2;

      let rangeStart =
        props.pages <= minPaginationElems
          ? 1
          : props.modelValue - props.rangeSize;
      let rangeEnd =
        props.pages <= minPaginationElems
          ? props.pages
          : props.modelValue + props.rangeSize;

      rangeEnd = rangeEnd > props.pages ? props.pages : rangeEnd;
      rangeStart = rangeStart < 1 ? 1 : rangeStart;

      if (props.pages > minPaginationElems) {
        const isStartBoundaryReached = rangeStart - 1 < 3;
        const isEndBoundaryReached = props.pages - rangeEnd < 3;

        if (isStartBoundaryReached) {
          rangeEnd = minPaginationElems - 2;
          for (let i = 1; i < rangeStart; i++) {
            res.push(i);
          }
        } else {
          res.push(1);
          res.push(null);
        }

        if (isEndBoundaryReached) {
          rangeStart = props.pages - (minPaginationElems - 3);
          for (let i = rangeStart; i <= props.pages; i++) {
            res.push(i);
          }
        } else {
          for (let i = rangeStart; i <= rangeEnd; i++) {
            res.push(i);
          }
          res.push(null);
          res.push(props.pages);
        }
      } else {
        for (let i = rangeStart; i <= rangeEnd; i++) {
          res.push(i);
        }
      }

      return res;
    });

    const isPrevControlsActive = computed(() => {
      return props.modelValue > 1;
    });

    const isNextControlsActive = computed(() => {
      return props.modelValue < props.pages;
    });

    function updatePageHandler(params) {
      emit("updateModelValue", params);
    }

    function goToPrev() {
      if (isPrevControlsActive.value) {
        emit("updateModelValue", props.modelValue - 1);
      }
      store.dispatch("toggleLoading");
    }

    function goToNext() {
      if (isNextControlsActive.value) {
        emit("updateModelValue", props.modelValue + 1);
      }
      store.dispatch("toggleLoading");
    }

    return {
      pagination,
      isPrevControlsActive,
      isNextControlsActive,
      isLoading,
      updatePageHandler,
      goToPrev,
      goToNext,
    };
  },
});
</script>
