import { createRouter, createWebHashHistory } from "vue-router";
import UserList from "../views/UserList.vue";

const routes = [
  {
    path: "/",
    name: "root",
    redirect: "/users",
  },
  {
    path: "/users",
    name: "Users",
    component: UserList,
  },
  //vue3 需要使用正則表達式
  {
    path: "/:catchAll(.*)",
    name: "404",
    component: () => import("../views/404.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
