<template>
  <div
    id="defaultModal"
    aria-hidden="true"
    :class="{
      hidden: !isModalOpen,
      'bg-gradient-to-b from-green-100 to-indigo-300': gender === 'male',
      'bg-gradient-to-b from-yellow-100 to-pink-300': gender === 'female',
    }"
    class="border shadow-lg rounded-md bg-white z-50"
  >
    <!-- Modal header -->
    <div
      class="flex justify-between items-start p-5 rounded-t border-b dark:border-gray-600"
    >
      <h3
        class="text-xl font-semibold text-gray-900 lg:text-2xl dark:text-white"
      >
        {{ fullName }}
      </h3>
      <button
        @click.stop.prevent="toggleModal({})"
        type="button"
        class="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center dark:hover:bg-gray-600 dark:hover:text-white"
        data-modal-toggle="defaultModal"
      >
        <svg
          class="w-5 h-5"
          fill="currentColor"
          viewBox="0 0 20 20"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
            clip-rule="evenodd"
          ></path>
        </svg>
      </button>
    </div>
    <!-- Modal body -->
    <div class="flex-col justify-center p-6 space-y-6">
      <img
        class="object-cover w-auto rounded-full mx-auto animate-bounce"
        :src="photo || 'https://fakeimg.pl/200x200/d1d1d1/'"
        alt="user-photo"
        aria-describedby="user-photo"
      />
      <p
        class="px-3 text-base leading-relaxed text-gray-500 dark:text-gray-400 text-left"
      >
        Hi, I am
        <span class="text-white bg-indigo-400">{{ `${fullName}` }}</span> ! I
        live in
        <span class="text-white bg-indigo-400">{{ `${city}` }}</span> and love
        to meet new friends. If you come to my city, feel free to contact me
        through <span class="text-white bg-indigo-400">{{ `${email}` }}</span> .
        Nice to meet you 😄
      </p>
    </div>
  </div>
</template>

<script>
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";
export default defineComponent({
  setup() {
    const store = useStore();
    const isModalOpen = computed(
      () => store.state.modalOpenModules.isModalOpen
    );
    const user = computed(() => store.state.modalOpenModules.user);
    const fullName =
      `${user.value.name.first} ${user.value.name.last}` || "fullName";
    const gender = user.value.gender;
    const photo = user.value.picture.large;
    const city = user.value.location.city || "city";
    const email = user.value.email || "abc@example.com";
    function toggleModal(user) {
      store.dispatch("toggleModal", user);
    }

    return { fullName, gender, photo, city, email, isModalOpen, toggleModal };
  },
});
</script>
