<template>
  <div class="w-full flex justify-end">
    <router-link to="" class="group" @click.stop.prevent="changeGrid">
      <i
        :class="{ 'text-green-500': isCardMode }"
        class="cursor-pointer group-hover:text-red-400 fa-2x fas fa-border-all mr-2"
      ></i
    ></router-link>
    <router-link to="" class="group" @click.stop.prevent="changeList">
      <i
        :class="{ 'text-green-500': !isCardMode }"
        class="cursor-pointer group-hover:text-red-400 fa-2x fas fa-list"
      ></i>
    </router-link>
  </div>
</template>

<script>
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";
export default defineComponent({
  setup() {
    const store = useStore();
    const isCardMode = computed(() => store.state.cardModeModules.isCardMode);
    function changeGrid() {
      store.dispatch("toGrid");
    }
    function changeList() {
      store.dispatch("toList");
    }
    return { changeGrid, changeList, isCardMode };
  },
});
</script>
