export default {
  state: {
    isModalOpen: false,
    user: {},
  },
  mutations: {
    TOGGLE_MODAL(state, data) {
      state.isModalOpen = !state.isModalOpen;
      // update modal content
      state.user = data;
    },
  },
  actions: {
    toggleModal({ commit }, data) {
      commit("TOGGLE_MODAL", data);
    },
  },
};
