<template>
  <div
    :class="{ hidden: !isModalOpen }"
    class="fixed inset-0 bg-gray-600 bg-opacity-80 overflow-y-auto h-full w-full"
    id="user-modal"
    aria-hidden="true"
  ></div>
</template>

<script>
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";
export default defineComponent({
  setup() {
    const store = useStore();
    const isModalOpen = computed(
      () => store.state.modalOpenModules.isModalOpen
    );
    return { isModalOpen };
  },
});
</script>
