export default {
  state: {
    isCardMode: true,
  },
  mutations: {
    GRID(state) {
      state.isCardMode = true;
    },
    LIST(state) {
      state.isCardMode = false;
    },
  },
  actions: {
    toGrid({ commit }) {
      commit("GRID");
    },
    toList({ commit }) {
      commit("LIST");
    },
  },
};
