<template>
  <li class="group">
    <span
      v-if="page === null"
      class="flex items-center justify-center bg-white w-6"
    >
      ...
    </span>
    <button
      v-else
      class="flex items-center justify-center w-6 h-6 text-sm rounded select-non group-hover:text-red-400"
      :class="{ 'cursor-default': page === current }"
      :disabled="isLoading || page === current"
      :aria-label="`Go to page ${page}`"
      :style="`background-color: ${isActive ? activeColor : 'transparent'};`"
      @click="clickHandler"
    >
      {{ page }}
    </button>
  </li>
</template>

<script>
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";
export default defineComponent({
  name: "VPage",
  props: {
    page: {
      type: Number,
      default: null,
    },
    current: {
      type: Number,
      default: 0,
    },
    activeColor: {
      type: String,
      default: "#DCEDFF",
    },
  },
  emits: ["update"],

  setup(props, { emit }) {
    const store = useStore();
    const isLoading = computed(() => store.state.loadingModules.isLoading);
    const isActive = computed(() => {
      return props.page === props.current;
    });
    function clickHandler() {
      store.dispatch("toggleLoading");
      emit("update", props.page);
    }

    return { isLoading, isActive, clickHandler };
  },
});
</script>
